import React, { useEffect, useState } from "react";
import {
  Button,
  CircularProgress,
  Drawer,
  Grid,
  IconButton,
  Skeleton,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import Chart from "../Buy/Chart";
import { useFormik } from "formik";
import {
  createOrder,
  getOrderList,
  getOrderListExecuted,
} from "../../../redux/reducers/orders";
import { makeBuy } from "../../../redux/reducers/buy1";
import { makeSell } from "../../../redux/reducers/sell1";
import SnackBar from "../../../components/SnackBar";
import Currencies from "./Currencies";
import Fields from "./Fields";
import Orders from "./Orders";
import { OrderAlert } from "../../../components/Orders";
import {
  setBalanceGlobal,
  setCurrenciesDrawer,
} from "../../../redux/reducers/Main";
import { useParams } from "react-router";
import { getBalances } from "../../../redux/reducers/myProfile";
import { useMediaQuery, useTheme } from "@mui/material";
import { RightIcon } from "../../../assets/images/icons";

const Trade = ({ t }) => {
  const dispatch = useDispatch();
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down(992));
  const md = useMediaQuery(theme.breakpoints.down(768));

  const { balance, isCurrenciesDraderOpen } = useSelector(
    ({ MainReducer }) => MainReducer
  );
  const { СurrenciesList, СurrenciesLoading } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const { allBalances } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  const { order_data } = useSelector(({ orders }) => orders);

  const { currencyId } = useParams();

  const [order, setOrder] = useState("market");
  const [loading, setLoading] = useState(false);
  const [dealType, setDealType] = useState("buy");
  const [response, setResponse] = useState({});
  const [currency, setCurrency] = useState({ currency: "" });
  const [drawerOpen, setDrawerOpen] = useState(false);

  const formik = useFormik({
    initialValues: {
      amountInUSDT: "",
      total: "",
      price: "",
      stopPrice: "",
    },
    onSubmit: (values) => {
      setLoading(true);
      if (order !== "market") {
        if (order === "limit") {
          const data = {
            order_type: order,
            order_side: dealType,
            currency: currency?.currency,
            price: values.price,
            amount: values.amountInUSDT,
            quantity: values.total,
          };
          dispatch(createOrder(data));
        } else {
          const data = {
            order_type: order,
            order_side: dealType,
            currency: currency?.currency,
            price: values.price,
            amount: values.amountInUSDT,
            quantity: values.total,
            stop_price: values.stopPrice,
          };
          dispatch(createOrder(data));
        }
      } else {
        const data = { sum: values.amountInUSDT, currency: currency?.currency };
        if (dealType === "buy") dispatch(makeBuy(data));
        else dispatch(makeSell(data));
      }
    },
  });

  const values = formik.values;

  useEffect(() => {
    dispatch(getOrderList());
    dispatch(getOrderListExecuted());
    setCurrency({ ...currency, currency: currencyId });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const usdt = allBalances?.find((item) => item.name === "USDT");
    dispatch(setBalanceGlobal(usdt?.balance));
    // eslint - disable - next - line;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allBalances]);

  useEffect(() => {
    if (Array.isArray(СurrenciesList)) {
      const crypto = СurrenciesList.find(
        (item) => item.currency === currencyId
      );

      dispatch(
        setCurrenciesDrawer({
          currency: crypto?.currency,
          name: crypto?.name,
          logo: crypto?.logo,
          open: false,
        })
      );

      formik.setFieldValue("price", crypto?.rate);
      setCurrency(crypto);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [СurrenciesList]);

  useEffect(() => {
    setLoading(false);
    if (order_data.severity === 200) {
      dispatch(getOrderList());
      dispatch(getBalances());
    }

    // eslint-disable-next-line
  }, [order_data]);

  return (
    <>
      <SnackBar state={response} setState={setResponse} />
      <OrderAlert />
      <Box
        component="section"
        backgroundColor="#F7F8F9"
        color="#FFF"
        sx={{
          pt: { xs: 0, md: 2 },
          pl: 2,
          pr: 2,
          minHeight: "calc(100vh - 56px)",
          overflowY: "hidden",

          "& *": {
            color: "inherit",
          },

          "& input": {
            color: "",
          },

          "& .MuiTextField-root": {
            mb: 1,
          },

          "& .MuiOutlinedInput-root": {
            pr: 0,
          },

          "& .MuiFormControl-root": {
            background: "#FFFFFF",
            boxShadow: "0px 1px 0px #E2E8F0",
            borderRadius: "8px",
          },

          "& .MuiInputAdornment-positionEnd": {
            background: "#E7EAEE",
            height: "100%",
            padding: "29.4px 20px",
            borderRadius: "0 8px 8px 0",
            maxWidth: 85,
            minWidth: 85,

            "& .MuiTypography-root": {
              color: "#000",
              fontWeight: 600,
            },
          },

          "& fieldset": {
            borderColor: "transparent",
          },
        }}
      >
        <Drawer
          anchor="left"
          open={isCurrenciesDraderOpen.open}
          onClose={() =>
            dispatch(
              setCurrenciesDrawer({ ...isCurrenciesDraderOpen, open: false })
            )
          }
          sx={{
            "& .MuiPaper-root": {
              width: { xs: "100%", lg: "30%" },
              p: 2,
              background: "#FFFFFF",
              borderRadius: "0 8px 8px 0",
            },
          }}
        >
          <Currencies
            currency={currency}
            t={t}
            setCurrency={setCurrency}
            formik={formik}
          />
        </Drawer>
        <Drawer
          anchor="bottom"
          open={drawerOpen}
          onClose={() => {
            if (!loading) setDrawerOpen(false);
          }}
          sx={{
            "& .MuiPaper-root": {
              width: { xs: "100%", lg: "30%" },
              p: 2,
              background: "#FFFFFF",
              borderRadius: "8px 8px 0 0",
            },
            "& *": {
              color: "inherit",
            },

            "& input": {
              color: "",
            },

            "& .MuiTextField-root": {
              mb: 1,
            },

            "& .MuiOutlinedInput-root": {
              pr: 0,
            },

            "& .MuiFormControl-root": {
              background: "#FFFFFF",
              boxShadow: "0px 1px 0px #E2E8F0",
              borderRadius: "8px",
            },

            "& .MuiInputAdornment-positionEnd": {
              background: "#E7EAEE",
              height: "100%",
              padding: "29.4px 20px",
              borderRadius: "0 8px 8px 0",
              maxWidth: 85,
              minWidth: 85,

              "& .MuiTypography-root": {
                color: "#000",
                fontWeight: 600,
              },
            },

            "& fieldset": {
              borderColor: "transparent",
            },
          }}
        >
          <Fields
            loading={loading}
            setLoading={setLoading}
            СurrenciesLoading={СurrenciesLoading}
            currency={currency}
            setDealType={setDealType}
            dealType={dealType}
            values={values}
            setResponse={setResponse}
            t={t}
            formik={formik}
            balance={balance}
            setOrder={setOrder}
            order={order}
          />
        </Drawer>
        {lg && (
          <Box
            mt={4}
            mb={2}
            display="flex"
            alignItems="center"
            onClick={() =>
              dispatch(
                setCurrenciesDrawer({ ...isCurrenciesDraderOpen, open: true })
              )
            }
          >
            {!СurrenciesLoading ? (
              <>
                <Skeleton
                  variant="circular"
                  width={36}
                  height={36}
                  sx={{ mr: 1 }}
                />
                <Skeleton height={24}  width={105} />
              </>
            ) : (
              <>
                <img src={currency?.logo} width="36px" height="36px" alt="" />
                <Typography variant="h3" ml={2} fontWeight={600} color="#000">
                  {currency?.currency} / USDT
                </Typography>
                <IconButton>
                  <RightIcon />
                </IconButton>
              </>
            )}
          </Box>
        )}
        <Grid container height="100%" sx={{ position: "relative" }} spacing={2}>
          <Grid item sx={{ minHeight: 575 }} xs={12} sm={12} md={9}>
            {!СurrenciesLoading ? (
              <Skeleton
                width="100%"
                sx={{
                  pt: 1,
                  transform: "none!important",
                }}
                height="555px"
              />
            ) : (
              <Chart coin={currencyId} />
            )}
          </Grid>
          {!md && (
            <Grid item xs={12} sm={3} md={3}>
              <Fields
                loading={loading}
                setLoading={setLoading}
                СurrenciesLoading={СurrenciesLoading}
                currency={currency}
                setDealType={setDealType}
                dealType={dealType}
                values={values}
                setResponse={setResponse}
                t={t}
                formik={formik}
                balance={balance}
                setOrder={setOrder}
                order={order}
              />
            </Grid>
          )}
        </Grid>
        <Orders t={t} />
        {md && (
          <Box
            sx={{
              position: "fixed",
              width: "100%",
              height: "100px",
              background: "#FFF",
              bottom: 0,
              left: 0,
              display: "flex",
              columnGap: 2,
              p: 2,
              zIndex: 1,
            }}
          >
            <Button
              fullWidth
              onClick={() => {
                setDrawerOpen(true);
                setDealType("buy");
              }}
              variant="contained"
              size="large"
              color="success"
            >
              {loading ? <CircularProgress color="secondary" /> : t("buy")}
            </Button>
            <Button
              fullWidth
              onClick={() => {
                setDrawerOpen(true);
                setDealType("sell");
              }}
              variant="contained"
              size="large"
              color="error"
            >
              {loading ? <CircularProgress color="secondary" /> : t("sell")}
            </Button>
          </Box>
        )}
      </Box>
    </>
  );
};

export default Trade;
