import React from "react";
import { Box, TextField, Typography } from "@mui/material";
import reload from "../../assets/images/statistic/reload.svg";
import { Link } from "react-router-dom";
import { makeStyles } from "@mui/styles";

const CabinetTop = ({
  title,
  input,
  courses,
  placeHolder,
  last_update,
  setFilter,
  payment,
  t,
}) => {
  const classes = useStyles();
  return (
    <Box className={classes.box} style={{ marginBottom: 20 }}>
      {payment ? (
        <Link className={classes.title} to="/dashboard/payment-for-services">
          {title}
        </Link>
      ) : (
        <Typography variant="h5" className={classes.title}>
          {title}
        </Typography>
      )}
      <Box className={classes.searchBox}>
        {courses && (
          <Box
            display="flex"
            className="searchBoxItem"
            alignItems="center"
            sx={{ marginRight: "20px" }}
          >
            <img src={reload} style={{ marginRight: 8 }} alt="" />
            <Typography
              variant="h5"
              style={{ color: "#64748B", fontWeight: "normal" }}
            >
              {t("updatedIn")} {last_update}
            </Typography>
          </Box>
        )}
        {input && (
          <div className="searchIcon">
            <TextField
            fullWidth
              className={classes.SearchInput}
              variant="outlined"
              onChange={(e) => setFilter(e.target.value)}
              label={!placeHolder ? t("SearchCoins") : placeHolder}
            />
          </div>
        )}
      </Box>
    </Box>
  );
};

export default CabinetTop;

const useStyles = makeStyles((theme) => ({
  SearchInput: {
    background: "#FFFFFF",
    boxShadow: "0px 4px 10px #E2E8F0",
    borderRadius: 8,
    width: 275,
    [theme.breakpoints.down(992)]: {
      width: "100%",
    },
  },

  searchBox: {
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.down(992)]: {
      flexDirection: "column-reverse",
      alignItems: "flex-start",
      width: "100%",

      "& .searchIcon": {
        margin: "0!important",
      },

      "& .searchBoxItem": {
        marginTop: 17,
      },
    },
  },
  title: {
    fontWeight: 500,
    fontSize: 23,

    [theme.breakpoints.down(992)]: {
      marginBottom: 16,
    },
  },

  box: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",

    [theme.breakpoints.down(992)]: {
      flexDirection: "column",
      alignItems: "flex-start",

      "& .searchIcon": {
        width: "100%",
      },
    },
  },
}));
