import { InputAdornment, TextField } from "@mui/material";
import React from "react";

const Inputs = ({ order, parameter, t, formik, handleValueChange }) => {
  return (
    <>
      {order === "limit" && (
        <TextField
          type="text"
          fullWidth
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">USDT</InputAdornment>
            ),

            startAdornment: (
              <InputAdornment position="start">{t("price")}:</InputAdornment>
            ),
            inputProps: {
              pattern: "\\d*.\\d*",
            },
          }}
          onKeyPress={(event) => {
            if (!/[0-9.]/.test(event.key)) {
              event.preventDefault();
            }
          }}
          variant="outlined"
          name="price"
          value={formik.values.price}
          onChange={handleValueChange}
        />
      )}

      {order === "stop-limit" ? (
        <>
          <TextField
            fullWidth
            type="text"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">{t("stop")}:</InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">USDT</InputAdornment>
              ),
              inputProps: {
                pattern: "\\d*.\\d*",
              },
            }}
            onKeyPress={(event) => {
              if (!/[0-9.]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            variant="outlined"
            name="stopPrice"
            value={formik.values.stopPrice}
            onChange={handleValueChange}
          />

          <TextField
            fullWidth
            type="text"
            InputProps={{
              inputProps: {
                pattern: "\\d*.\\d*",
              },
              startAdornment: (
                <InputAdornment position="start">{t("price")}:</InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">USDT</InputAdornment>
              ),
            }}
            onKeyPress={(event) => {
              if (!/[0-9.]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            variant="outlined"
            name="price"
            value={formik.values.price}
            onChange={handleValueChange}
          />
        </>
      ) : null}
    </>
  );
};

export default Inputs;
