import {
  Container,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
} from "@mui/material";
import { makeStyles } from "@material-ui/styles";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Balance from "../../../components/Balance";
import { OrderAlert } from "../../../components/Orders";
import {
  cancelOrder,
  getOrderList,
  getOrderListExecuted,
} from "../../../redux/reducers/orders";
import { MediumTitle } from "../../../shared/Typography";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const OrderHistory = ({ t }) => {
  const dispatch = useDispatch();
  const classes = useStyles();

  const [state, setState] = useState(false);

  useEffect(() => {
    dispatch(getOrderList());
    dispatch(getOrderListExecuted());
    dispatch(getСurrenciesList());
    // eslint-disable-next-line
  }, []);

  const { order_list, order_list_executed } = useSelector(
    ({ orders }) => orders
  );

  useEffect(() => {
    if (state) {
      setTimeout(() => {
        dispatch(getOrderList());
        dispatch(getOrderListExecuted());
        dispatch(getСurrenciesList());
      }, 1000);
      setState(false);
    }
    // eslint-disable-next-line
  }, [state]);

  return (
    <section>
      <OrderAlert />
      <Container maxWidth="lg">
        <Balance />
        <MediumTitle style={{ marginBottom: 16 }}>{t("order")}</MediumTitle>
        <TableContainer className={classes.TableContainer} component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table" size="small">
            <TableHead>
              <TableRow>
                <TableCell align="center">{t("type")}</TableCell>
                <TableCell align="center">{t("side")}</TableCell>
                <TableCell align="center">{t("pair")}</TableCell>
                <TableCell align="center">{t("Amount")}</TableCell>
                <TableCell align="center">{t("quantity")}</TableCell>
                <TableCell align="center">{t("condition")}</TableCell>
                <TableCell align="center">{t("action")}</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {order_list?.orders
                // eslint-disable-next-line
                ?.filter((item) => item.status.value != 3)
                .map((item, idx) => (
                  <TableRow
                    key={idx}
                    sx={{
                      "&:last-child td, &:last-child th": { border: 0 },
                    }}
                  >
                    <TableCell align="center">{item.type}</TableCell>
                    <TableCell align="center">{item.side}</TableCell>
                    <TableCell align="center">{item.currency}/USDT</TableCell>
                    <TableCell align="center">{item.sum}</TableCell>
                    <TableCell align="center">{item.quantity}</TableCell>
                    <TableCell align="center">
                      {item.side === "Buy" || item.side === "Покупка"
                        ? ">="
                        : "<="}
                      &#160;
                      {item.price}
                    </TableCell>
                    <TableCell align="center">
                      <Button
                        size="large"
                        onClick={() => {
                          setState(true);
                          dispatch(cancelOrder(item.id));
                        }}
                        color="primary"
                        variant="contained"
                      >
                        {t("cancel")}
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>

        <MediumTitle style={{ marginTop: 40, marginBottom: 16 }}>
          {t("historyOrder")}
        </MediumTitle>
        <TableContainer className={classes.TableContainer} component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table" size="small">
            <TableHead>
              <TableRow>
                <TableCell align="center">{t("type")}</TableCell>
                <TableCell align="center">{t("side")}</TableCell>
                <TableCell align="center">{t("pair")}</TableCell>
                <TableCell align="center">{t("Amount")}</TableCell>
                <TableCell align="center">{t("quantity")}</TableCell>
                <TableCell align="center">{t("condition")}</TableCell>
                <TableCell align="center">{t("action")}</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {order_list_executed?.orders?.map((item, idx) => (
                <TableRow
                  key={idx}
                  sx={{
                    "&:last-child td, &:last-child th": { border: 0 },
                  }}
                >
                  <TableCell align="center">{item.type}</TableCell>
                  <TableCell align="center">{item.side}</TableCell>
                  <TableCell align="center">{item.currency}/USDT</TableCell>
                  <TableCell align="center">{item.sum}</TableCell>
                  <TableCell align="center">{item.quantity}</TableCell>
                  <TableCell align="center">
                    {item.side === "Buy" || item.side === "Покупка"
                      ? ">="
                      : "<="}
                    &#160;
                    {item.price}
                  </TableCell>
                  <TableCell align="center">
                    <Button
                      size="large"
                      disabled
                      color="primary"
                      variant="contained"
                    >
                      {t("cancel")}
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Container>
    </section>
  );
};

export default OrderHistory;

const useStyles = makeStyles((theme) => ({
  TableContainer: {
    background: "#FFFFFF",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "12px",
  },
}));
