import React from "react";
import {
  Button,
  Container,
  Snackbar,
  styled,
  TextareaAutosize,
  Typography,
} from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { MediumTitle } from "../../shared/Typography";
import Rating from "@mui/material/Rating";
import { Box } from "@mui/system";
import star from "../../assets/images/state/starOutlined.svg";
import starContained from "../../assets/images/state/starContained.svg";
import { Form, Formik } from "formik";
import { Alert } from "@mui/material";
import { postReview, setAddedReview } from "../../redux/reducers/reviews";
import { makeStyles } from "@mui/styles";

const LeaveFeedback = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { addedReview } = useSelector(({ reviewsReducer }) => reviewsReducer);

  const StyledRating = styled(Rating)({
    "& .MuiRating-iconFilled": {
      color: "#ff6d75",
    },
    "& .MuiRating-iconHover": {
      color: "#ff3d47",
    },
  });

  return (
    <section className={classes.section}>
      <Container maxWidth="lg">
        <Snackbar
          open={addedReview?.open}
          autoHideDuration={5000}
          onClose={() =>
            dispatch(setAddedReview({ ...addedReview, open: false }))
          }
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
        >
          <Alert
            severity={addedReview?.severity === 200 ? "success" : "error"}
            color={addedReview?.severity === 200 ? "success" : "error"}
            variant="filled"
          >
            {addedReview?.message?.messages}
          </Alert>
        </Snackbar>
        <Formik
          initialValues={{
            fullname: "",
            email: "",
            message: "",
          }}
          onSubmit={(values) => {
            dispatch(postReview(values));
          }}
        >
          {({ values, handleChange }) => (
            <Form>
              <Typography variant="h2" className={classes.title}>
                {t("leaveReview")}
              </Typography>
              <MediumTitle style={{ textAlign: "center", marginBottom: 16 }}>
                {t("rate")}
              </MediumTitle>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexDirection: "column",
                }}
              >
                <StyledRating
                  name="rating"
                  value={values.rating}
                  onChange={handleChange}
                  defaultValue={2}
                  precision={1}
                  icon={<img src={starContained} alt="" />}
                  emptyIcon={<img src={star} alt="" />}
                />
              </Box>
              <MediumTitle className={classes.label}>
                {t("Commentary")}
              </MediumTitle>
              <TextareaAutosize
                name="message"
                value={values.message}
                onChange={handleChange}
                placeholder={t("WriteComment")}
                className={classes.textarea}
                required
              />
              <Button
                variant="contained"
                className={classes.button}
                size="large"
                type="submit"
                color="secondary"
              >
                {t("leaveReview")}
              </Button>
            </Form>
          )}
        </Formik>
      </Container>
    </section>
  );
};

export default LeaveFeedback;

const useStyles = makeStyles((theme) => ({
  title: {
    color: "#000000",
    marginBottom: 40,

    [theme.breakpoints.down(768)]: {
      textAlign: "center",
      marginBottom: 16,
    },
  },

  button: {
    margin: "52px auto 0",
    display: "block",
    [theme.breakpoints.down(768)]: {
      width: "100%",
    },
  },

  section: {
    padding: "80px 0",
    background: "#F7F8F9",
  },

  label: {
    [theme.breakpoints.down(768)]: {
      marginTop: 16,
    },
  },
  textarea: {
    padding: "16px 24px",
    outline: "none",
    width: "100%",
    height: "250px!important",
    background: "#FFFFFF",
    marginTop: 8,
    resize: "vertical",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "8px",
    fontSize: 20,
    transition: "all 0.2s linear",
    border: "2px solid transparent",

    "&:focus": {
      border: "2px solid #FCE34D",
    },
  },
}));
