import { Dialog, Typography, Button, CircularProgress } from "@mui/material";
import React, { useLayoutEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import {
  confirmSell,
  setSellConfirmationData,
  setSellData,
} from "../../../redux/reducers/sell1";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const SellConfirm = ({ t, setLoadingMain }) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [loading, setLoading] = useState(false);

  const firstUpdate = useRef(true);

  const { sell, sell_confirmation } = useSelector(({ sell }) => sell);

  const confirm = () => {
    // confirm transaction
    dispatch(confirmSell(sell?.message?.confirm?.id));
    // show loader
    setLoading(true);
  };

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (sell_confirmation.severity === 200) {
      dispatch(setSellConfirmationData({ ...sell_confirmation, open: false }));
      dispatch(setSellData({ ...sell, open: false }));

      // update balances
      dispatch(getСurrenciesList());

      setTimeout(() => {
        // navigate to history
        history.push("/dashboard/history");
      }, 500);
    }

    if (sell_confirmation.severity) {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [sell_confirmation.severity]);

  return (
    <>
      <Dialog
        open={sell.open}
        onClose={() => {
          if (!loading) {
            setLoadingMain(false)
            dispatch(
              setSellData({
                ...sell,
                open: false,
              })
            );
          }
        }}
        scroll="body"
        sx={{
          "& .MuiPaper-root": {
            borderRadius: 2,
            width: "100%",
            padding: "40px 31px!important",
          },

          "& ul": {
            "& li": {
              fontWeight: 300,
              mb: 2,
              display: "flex",
              justifyContent: "space-between",
              whiteSpace: "nowrap",
              alignItems: "baseline",

              "& div": {
                width: "100%",
                height: "fit-content",
                borderBottom: "2px dotted #B8C0CC",
                margin: "0 3px",
              },

              "& span": {
                fontWeight: 500,
              },
            },
          },
        }}
      >
        <Typography variant="h2" fontWeight="500" textAlign="center" mb={3}>
          {t("paymentСonfirmation")}
        </Typography>
        <ul style={{ listStyle: "none" }}>
          <Typography variant="h5" component="li">
            {t("amountToBeCredited")}
            <div></div>
            <span>{sell?.message?.confirm?.debit} USDT</span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("amountToPay")}
            <div></div>
            <span>
              {sell?.message?.confirm?.amount}{" "}
              {sell?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("Commission")}
            <div></div>
            <span>{sell?.message?.confirm?.commission} USDT</span>
          </Typography>
        </ul>
        <Button
          fullWidth
          variant="contained"
          color="primary"
          size="large"
          onClick={confirm}
          disabled={loading}
        >
          {loading ? (
            <CircularProgress className="progress" color="primary" />
          ) : (
            t("confirm")
          )}
        </Button>
      </Dialog>
    </>
  );
};

export default SellConfirm;
