import React, { useEffect } from "react";
import { Box, Typography, Button } from "@mui/material";
import { useHistory } from "react-router";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setBalanceGlobal } from "../../redux/reducers/Main";
import tether from "./../../assets/images/crypto/tetherIcon.webp";

const Balance = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  const { t } = useTranslation();

  const { balance } = useSelector(({ MainReducer }) => MainReducer);
  const { allBalances } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  useEffect(() => {
    const usdt = allBalances?.find((item) => item.name === "USDT");
    dispatch(setBalanceGlobal(usdt?.balance));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allBalances]);

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      flexDirection={{ xs: "column", sm: "row" }}
      borderBottom="2px solid #E7EAEE"
      pb={{ xs: 2, sm: 4, md: 5 }}
      mb={{ xs: 2, sm: 3, md: 5 }}
    >
      <div>
        <Typography variant="h4" color="#323A46" fontWeight="500">
          {t("YourBalance")}
        </Typography>
        <Typography
          variant="h2"
          fontWeight="500"
          sx={{
            display: "flex",
            alignItems: "center",
          }}
        >
          <img width="51" src={tether} alt="tether" />
          &#160;
          {balance}
        </Typography>
      </div>
      <Box width={{ xs: "100%", sm: "fit-content" }} mt={{ xs: 2, md: 0 }}>
        <Button
          variant="contained"
          onClick={() => history.push("/dashboard/payment-for-services/106")}
          fullWidth
          size="large"
          color="primary"
        >
          {t("WithdrawFunds")}
        </Button>
        <Box mt={1}>
          <Button
            variant="contained"
            fullWidth
            onClick={() => history.push("/dashboard/replenish")}
            size="large"
            color="secondary"
          >
            {t("TopUpBalance")}
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default Balance;
