import React, { useEffect, useState } from "react";
import { Link, NavLink, useHistory, useLocation } from "react-router-dom";
import logo from "../../assets/images/header/logo.webp";
import LanguageSelect from "../../components/language";
import "./index.css";
import { useDispatch, useSelector } from "react-redux";
import BasicPopover from "./Popover";
import menu from "../../assets/images/header/menu.svg";
import cookie from "cookie_js";
import { setCurrenciesDrawer, setDrawerOpen } from "../../redux/reducers/Main";
import LogoutIcon from "@mui/icons-material/Logout";
import { getMyDealsProccess } from "../../redux/reducers/P2P";
import {
  Box,
  ListItem,
  Popover,
  Container,
  Typography,
  useMediaQuery,
  useTheme,
  IconButton,
  List,
  Button,
  Skeleton,
} from "@mui/material";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import {
  MainIcon,
  PaymentsIcon,
  SellIcon,
  TopUpIcon,
  TradeIcon,
  WithdrawIcon,
} from "../../assets/images/icons";
import { makeStyles } from "@mui/styles";
import Currency from "../../components/Currency";

const HeaderCabinet = ({ t, i18n }) => {
  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  const { isDrawerOpen, isCurrenciesDraderOpen } = useSelector(
    ({ MainReducer }) => MainReducer
  );

  const { myDealsProcess } = useSelector(({ P2PReducer }) => P2PReducer);

  const domain = window.location.hostname;
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));
  const sm = useMediaQuery(theme.breakpoints.down(768));
  const dispatch = useDispatch();
  const location = useLocation();
  const [isOpened, setIsOpened] = useState(false);
  const classes = useStyles();
  const history = useHistory();

  const logout = () => {
    cookie.removeSpecific("token_netex", {
      path: "/",
      domain: domain,
    });
    setTimeout(() => {
      window.location.reload();
    }, 200);
  };

  useEffect(() => {
    dispatch(getMyDealsProccess());
    // eslint-disable-next-line
  }, []);

  return (
    <header
      className="header-cabinet"
      style={{
        paddingBottom: location.pathname.includes("/dashboard/trade") && 0,
        color: "#FFF",
      }}
    >
      <Container>
        <div className="header__inner">
          <Link to="/">
            <img
              src={logo}
              style={sm ? { height: 34 } : { height: 42, marginRight: 45 }}
              alt="logo"
            />
          </Link>
          <div className="header__profile">
            {!tablet && (
              <Link to="/dashboard/settings/profile">
                <Typography variant="body1" style={{ textAlign: "right" }}>
                  {profileData?.data?.profile?.firstname}{" "}
                  {profileData?.data?.profile?.lastname}
                </Typography>
                <Typography
                  style={{
                    fontSize: 17,
                    color: "#B8C0CC",
                    textAlign: "right",
                  }}
                >
                  {profileData?.data?.profile?.email}
                </Typography>
              </Link>
            )}
            <Box
              style={{ margin: "0 16px", zIndex: 10, display: "block" }}
              component={NavLink}
              onClick={() => dispatch(setDrawerOpen(false))}
              to="/dashboard/main"
            >
              {!profileData?.data?.profile?.avatar ? (
                <AccountCircleIcon
                  sx={{ width: "60px", height: "60px" }}
                  color="primary"
                />
              ) : (
                <img
                  className="header__profile-avatar"
                  src={profileData?.data?.profile?.avatar}
                  alt=""
                />
              )}
            </Box>
            {!tablet && <LanguageSelect t={t} i18n={i18n} />}
             {/* <Currency /> */}
            {!tablet && (
              <IconButton style={{ color: "white" }} onClick={() => logout()}>
                <LogoutIcon style={{ color: "white" }} />
              </IconButton>
            )}
            {tablet && (
              <IconButton
                style={{ color: "white" }}
                onClick={() => dispatch(setDrawerOpen(!isDrawerOpen))}
              >
                <img
                  src={menu}
                  style={
                    isDrawerOpen
                      ? {
                          transform: "rotate(-90deg)",
                          zIndex: 1000000,
                          transition: "all 0.2s linear",
                        }
                      : {
                          transition: "all 0.2s linear",
                        }
                  }
                  alt=""
                />
              </IconButton>
            )}
          </div>
        </div>
      </Container>

      {!tablet && (
        <>
          {location.pathname.includes("/dashboard/trade") ? (
            <Nav
              t={t}
              myDealsProcess={myDealsProcess}
              classes={classes}
              location={location}
              setIsOpened={setIsOpened}
              isOpened={isOpened}
              history={history}
              trade
              dispatch={dispatch}
              isCurrenciesDraderOpen={isCurrenciesDraderOpen}
            />
          ) : (
            <Nav
              setIsOpened={setIsOpened}
              isOpened={isOpened}
              history={history}
              t={t}
              myDealsProcess={myDealsProcess}
              classes={classes}
              location={location}
              dispatch={dispatch}
              isCurrenciesDraderOpen={isCurrenciesDraderOpen}
            />
          )}
        </>
      )}
    </header>
  );
};

export default HeaderCabinet;

const Nav = ({
  t,
  history,
  classes,
  location,
  trade,
  dispatch,
  isCurrenciesDraderOpen,
}) => {
  const { СurrenciesLoading } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const [anchorEl, setAnchorEl] = useState(null);

  const open = Boolean(anchorEl);

  const id = open ? "simple-popover" : undefined;

  const handleClick = (event) => setAnchorEl(event.currentTarget);

  const handleClose = () => setAnchorEl(null);

  const openDrawer = () =>
    dispatch(setCurrenciesDrawer({ ...isCurrenciesDraderOpen, open: true }));

  const links = [
    {
      path: "main",
      txt: t("main"),
      icon: <MainIcon />,
      id: 0,
    },
    {
      path: "replenish/cryptocurrency",
      txt: t("topUp"),
      icon: <TopUpIcon />,
      condition: location.pathname.includes("replenish") ? "visited" : false,
      id: 1,
    },
    {
      path: "withdraw",
      txt: t("withdraw"),
      icon: <WithdrawIcon />,
      id: 2,
    },
    {
      txt: t("sell"),
      icon: <SellIcon />,
      path: "sell",

      // onClick: handleClick,
      condition: location.pathname.includes("dashboard/sell")
        ? ""
        : ` ${classes.container}`,
      id: 3,
    },
    {
      txt: t("buy"),
      icon: <TradeIcon />,
      path: "buy",

      // onClick: handleClick,
      condition: location.pathname.includes("dashboard/buy")
        ? ""
        : ` ${classes.container}`,
      id: 3,
    },
    // {
    //   txt: t("trade"),
    //   icon: <TradeIcon />,
    //   onClick: handleClick,
    //   condition:
    //     location.pathname.includes("dashboard/trade") ||
    //     location.pathname.includes("dashboard/exchange")
    //       ? ""
    //       : ` ${classes.container}`,
    //   id: 3,
    // },
    {
      path: "payment-for-services/",
      txt: t("payments"),
      icon: <PaymentsIcon />,
      id: 4,
    },
    // {
    //   path: "main/nft",
    //   txt: t("Netex NFT"),
    //   icon: "",
    //   soon: true,
    //   id: 5,
    // },
  ];

  return (
    <>
      <Box
        component="nav"
        sx={{
          color: "#FFF",
          backgroundColor: "#292929",
          maxHeight: "72px",
          height: " 100%",
          width: " 100%",
          position: "relative",
          "& .MuiContainer-root": {
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            height: " 100%",
            width: " 100%",

            "& a, .button": {
              display: " flex",
              alignItems: "center",
              justifyContent: "center",
              flexWrap: "nowrap",
              width: " 100%",
              position: " relative",
              cursor: "pointer",
              p: "14.5px",
              whiteSpace: "nowrap",

              "& svg": {
                mr: 1,
                height: 24,
              },

              "&.visited": {
                color: "var(--primary)",
                borderBottom: "3px solid var(--primary)",
                "& svg path": {
                  stroke: "var(--primary)",
                },
              },
            },
          },
        }}
      >
        <Container>
          {trade && (
            <ListItem button onClick={openDrawer}>
              {!СurrenciesLoading ? (
                <>
                  <Skeleton
                    variant="circular"
                    width={30}
                    height={30}
                    sx={{ color: "#FFF", backgroundColor: "#FFF", mr: 1 }}
                  />
                  <Skeleton
                    sx={{ color: "#FFF", backgroundColor: "#FFF" }}
                    width={105}
                  />
                </>
              ) : (
                <>
                  <img
                    src={isCurrenciesDraderOpen.logo}
                    style={{ objectFit: "contain", marginRight: 8 }}
                    width="30"
                    height="30"
                    alt=""
                  />
                  <Typography
                    fontWeight="800"
                    style={{
                      whiteSpace: "nowrap",
                    }}
                  >
                    {isCurrenciesDraderOpen.currency} / USDT
                  </Typography>
                </>
              )}

              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M8.9502 4.07968L15.4702 10.5997C16.2402 11.3697 16.2402 12.6297 15.4702 13.3997L8.9502 19.9197"
                  stroke="white"
                  stroke-width="2"
                  stroke-miterlimit="10"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            </ListItem>
          )}

          {links.map((item) =>
            item.onClick ? (
              <div
                onClick={item.onClick}
                className={
                  location.pathname.includes("exchange") ||
                  location.pathname.includes("trade")
                    ? `button visited`
                    : "button"
                }
              >
                {item.icon}
                {item.txt}
              </div>
            ) : (
              <NavLink
                to={!item.soon ? `/dashboard/${item.path}` : `/${item.path}`}
                className={item.condition && item.condition}
                activeClassName="visited"
              >
                {item.icon}
                <span>{item.txt}</span>
                {item.soon && (
                  <span
                    style={{
                      backgroundColor: "#f00",
                      objectFit: "contain",
                      position: "absolute",
                      bottom: 2,
                      right: 0,
                      padding: 4,
                      borderRadius: 4,
                      fontSize: 10,
                    }}
                  >
                    СКОРО!
                  </span>
                )}
              </NavLink>
            )
          )}

          <div style={{ justifyContent: "flex-end" }}>
            <BasicPopover t={t} />
          </div>
        </Container>{" "}
      </Box>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
      >
        <List buttonst className={classes.wrapper}>
          <ListItem button onClick={() => history.push("/dashboard/exchange")}>
            {t("quickExchange")}
          </ListItem>
          <ListItem button onClick={() => history.push("/dashboard/trade")}>
            {t("spot")}
          </ListItem>
        </List>
      </Popover>
    </>
  );
};

const useStyles = makeStyles((theme) => ({
  badge: {
    backgroundColor: "#EF4444",
    position: "absolute",
    top: 2,
    right: 2,
    borderRadius: "100%",
    fontSize: 16,
    fontWeight: "600",
    height: 20,
    color: "#fff",
    width: 20,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  container: {
    cursor: "pointer",
  },

  popover: {
    right: "0%",
    bottom: 0,
    zIndex: 100000,
    transform: "translateY(100%)",
    width: "100%",
    position: "absolute",
  },

  wrapper: {
    backgroundColor: "#000",
    color: "#FFF",
  },
}));
