import { Button, CircularProgress } from "@mui/material";
import { Box } from "@mui/system";
import React, { useLayoutEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import {
  cancelOrder,
  getOrderList,
  getOrderListExecuted,
} from "../../../../redux/reducers/orders";

const ActiveOrders = ({ order_list, t, order_data }) => {
  const dispatch = useDispatch();

  const firstUpdate = useRef(true);
  const [state, setState] = useState(false);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (order_data.severity === 200 && order_data.delete) {
      dispatch(getOrderList());
      dispatch(getOrderListExecuted());
      setTimeout(() => setState(false), 1000);
    }
  }, [order_data]);

  return (
    <Box
      component="table"
      sx={{
        width: { xs: "100%", lg: "90%" },
        m: "16px auto",
        background: "#FFFFFF",
        boxShadow: "0px 1px 0px #E2E8F0",
        borderRadius: "12px",
        "& thead td": {
          textAlign: "center",
          fontSize: 18,
          fontWeight: "500",
          color: "#64748B",
          p: "16px 32px",
        },

        "& tbody td": {
          textAlign: "center",
          fontWeight: "600",
          fontSize: 20,
          color: "#000",
          p: "8px 32px",
        },

        "& tbody tr": {
          height: 1,
          borderBottom: "1px solid #E7EAEE",
        },
      }}
    >
      <thead>
        <tr>
          <td>{t("type")}</td>
          <td>{t("side")}</td>
          <td>{t("pair")}</td>
          <td>{t("Amount")}</td>
          <td>{t("quantity")}</td>
          <td>{t("condition")}</td>
          <td>{t("action")}</td>
        </tr>
      </thead>

      <tbody>
        {order_list?.orders
          // eslint-disable-next-line
          ?.filter((item) => item.status.value != 3)
          .map((item) => (
            <tr>
              <td>{item.type}</td>
              <td>{item.side}</td>
              <td>{item.currency}/USDT</td>
              <td>{item.sum}</td>
              <td>{item.quantity}</td>
              <td>
                {item.side === "Buy" || item.side === "Покупка" ? ">=" : "<="}
                &#160;
                {item.price}
              </td>
              <td>
                <Button
                  size="small"
                  onClick={() => {
                    setState(true);
                    dispatch(cancelOrder(item.id));
                  }}
                  disabled={state}
                  color="primary"
                  variant="contained"
                >
                  {state ? (
                    <CircularProgress
                      sx={{
                        "&.MuiCircularProgress-root": {
                          width: "20px!important",
                          height: "20px!important",
                        },
                      }}
                      color="secondary"
                    />
                  ) : (
                    t("cancel")
                  )}
                </Button>
              </td>
            </tr>
          ))}
      </tbody>
    </Box>
  );
};

export default ActiveOrders;
