/* eslint-disable eqeqeq */
import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import {
  Select,
  Button,
  FormControl,
  MenuItem,
  TextField,
  IconButton,
  ClickAwayListener,
  Tooltip,
  CircularProgress,
  Skeleton,
  InputLabel,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Container, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import CompareArrowsIcon from "@mui/icons-material/CompareArrows";
import { useFormik } from "formik";
import { makeBuy } from "../../../redux/reducers/buy1";
import { makeSell } from "../../../redux/reducers/sell1";
import { QuestionIcon } from "../../../assets/images/icons";
import SnackBar from "../../../components/SnackBar";
import SellConfirm from "../sell/SellConfirm";
import ConfirmBuy from "../Buy/ConfrimBuy";

const Exchange = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [preloading, setPreloading] = useState(true);
  const [response, setResponse] = useState({});
  const [touchedUSDT, setTouchedUSDT] = useState(false);
  const [touched, setTouched] = useState(false);

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const { buy } = useSelector(({ buy }) => buy);
  const { sell } = useSelector(({ sell }) => sell);

  const formik = useFormik({
    initialValues: {
      currencyToSell: {
        id: "2",
      },
      currencyToBuy: {
        id: "1",
      },
      sumToSell: "",
      sumToBuy: "",
    },
    onSubmit: (values) => {
      setLoading(true);
      if (values.currencyToSell?.id == 1) {
        dispatch(
          makeBuy({
            sum: values.sumToBuy,
            currency: values.currencyToBuy.currency,
          })
        );
      } else {
        dispatch(
          makeSell({
            sum: values.sumToBuy,
            currency: values.currencyToSell.currency,
          })
        );
      }
    },
  });

  const values = formik.values;

  useEffect(() => {
    if (СurrenciesList[0]) setPreloading(false);
    // setTimeout(() => setPreloading(false), 1000);
    const usdt = СurrenciesList?.find((item) => item?.id == 1);
    formik.setFieldValue("currencyToSell", usdt);
    const btc = СurrenciesList?.find((item) => item?.id == 2);
    formik.setFieldValue("currencyToBuy", btc);
    // eslint-disable-next-line
  }, [СurrenciesList]);

  useEffect(() => {
    if (values.currencyToSell?.id == 1) {
      if (values.sumToBuy && touched) {
        formik.setFieldValue(
          "sumToSell",
          (values.sumToBuy / +values.currencyToBuy?.rate).toFixed(
            values.currencyToBuy?.decimal
          )
        );
      }

      if (values.sumToSell && touchedUSDT) {
        formik.setFieldValue(
          "sumToBuy",
          (values.sumToSell * +values.currencyToBuy?.rate).toFixed(
            values.currencyToSell?.decimal
          )
        );
      }
    } else {
      if (values.sumToBuy && touched) {
        formik.setFieldValue(
          "sumToSell",
          (values.sumToBuy * +values.currencyToSell?.rate).toFixed(
            values.currencyToBuy?.decimal
          )
        );
      }

      if (values.sumToSell && touchedUSDT) {
        formik.setFieldValue(
          "sumToBuy",
          (values.sumToSell / +values.currencyToSell?.rate).toFixed(
            values.currencyToSell?.decimal
          )
        );
      }
    }
    // eslint-disable-next-line
  }, [values.sumToBuy, values.sumToSell]);

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (values.currencyToSell?.id == 1)
      setResponse({
        ...buy,
        open: buy.severity === 200 ? false : true,
      });
    else
      setResponse({
        ...sell,
        open: sell.severity === 200 ? false : true,
      });
    // eslint-disable-next-line
  }, [buy, sell]);

  return (
    <>
      <ConfirmBuy setLoadingMain={setLoading} t={t} />
      <SellConfirm setLoadingMain={setLoading} t={t} />
      <SnackBar state={response} setState={setResponse} />
      <Box
        component="section"
        sx={{
          color: "#000",
        }}
      >
        <Container
          maxWidth="lg"
          sx={{
            minHeight: "66vh",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Typography variant="h3" mb={3} fontWeight="500" textAlign="center">
            {t("quickExchange")}
          </Typography>
          <Box mb={2} display={{ xs: "flex", md: "none" }} columnGap="8px">
            <Typography variant="body2" fontWeight="600">
              {t("balance")}
            </Typography>
            {preloading ? (
              <Skeleton width={100} />
            ) : (
              <>
                <img
                  src={values?.currencyToSell?.logo}
                  width="16px"
                  height="16px"
                  alt=""
                />
                <Typography variant="body1">
                  {values?.currencyToSell?.balance}{" "}
                  {values?.currencyToSell?.currency}
                </Typography>
              </>
            )}
          </Box>

          <Box
            component="form"
            width={{ xs: "100%", lg: "70%" }}
            onSubmit={formik.handleSubmit}
          >
            <Box display="flex" columnGap="16px" mb={2} alignItems="center">
              <FormControl fullWidth>
                <Select
                  value={values.currencyToSell}
                  name="currencyToSell"
                  onChange={formik.handleChange}
                  variant="outlined"
                >
                  {preloading ? (
                    <MenuItem selected value={1}>
                      <Skeleton width={100} />
                    </MenuItem>
                  ) : (
                    Array.isArray(СurrenciesList) &&
                    СurrenciesList
                      .filter((item) =>
                        values.currencyToSell?.id != 1
                          ? item.currency !== "USDT"
                          : item.currency === "USDT"
                      )
                      .map((item, idx) => (
                        <MenuItem key={idx} value={item}>
                          <img src={item.logo} className={classes.img} alt="" />
                          {item.currency}
                        </MenuItem>
                      ))
                  )}
                </Select>
              </FormControl>
              <IconButton
                onClick={() => {
                  formik.setFieldValue("currencyToBuy", values.currencyToSell);
                  formik.setFieldValue("currencyToSell", values.currencyToBuy);
                  formik.setFieldValue("sumToBuy", values.sumToSell);
                  formik.setFieldValue("sumToSell", values.sumToBuy);
                }}
              >
                <CompareArrowsIcon sx={{ fontSize: 40 }} />
              </IconButton>
              <FormControl fullWidth>
                <Select
                  value={values.currencyToBuy}
                  name="currencyToBuy"
                  onChange={formik.handleChange}
                  variant="outlined"
                >
                  {preloading ? (
                    <MenuItem selected value={1}>
                      <Skeleton width={100} />
                    </MenuItem>
                  ) : (
                    Array.isArray(СurrenciesList) &&
                    СurrenciesList
                      .filter((item) =>
                        values.currencyToBuy?.id != 1
                          ? item.currency !== "USDT"
                          : item.currency === "USDT"
                      )
                      .map((item, idx) => (
                        <MenuItem key={idx} value={item}>
                          <img src={item.logo} className={classes.img} alt="" />
                          {item.currency}
                        </MenuItem>
                      ))
                  )}
                </Select>
              </FormControl>
            </Box>
            <Box display="flex" columnGap="16px" mb={2} alignItems="center">
              <TextField
                name="sumToBuy"
                onClick={() => setTouched(true)}
                onBlur={() => setTouched(false)}
                value={values.sumToBuy}
                onChange={formik.handleChange}
                fullWidth
                label={t("IGive")}
                // InputProps={{
                //   endAdornment: (
                //     <InputAdornment
                //       position="end"
                //       className={classes.InputAdornment}
                //     >
                //       Максимум <div className="primary">0</div>
                //     </InputAdornment>
                //   ),
                // }}
                variant="outlined"
              />
              <TextField
                onClick={() => setTouchedUSDT(true)}
                onBlur={() => setTouchedUSDT(false)}
                name="sumToSell"
                value={values.sumToSell === "NaN" ? "0" : values.sumToSell}
                onChange={formik.handleChange}
                fullWidth
                label={t("IGet")}
                variant="outlined"
              />
            </Box>
            <Box
              display="flex"
              width={{ xs: "100%", lg: "100%" }}
              justifyContent="space-between"
            >
              <Box
                display={{ xs: "none", md: "flex" }}
                alignItems="center"
                columnGap="8px"
              >
                <Typography variant="body2" fontWeight="600">
                  {t("balance")}
                </Typography>
                {preloading ? (
                  <Skeleton width={100} />
                ) : (
                  <>
                    <img
                      src={values?.currencyToSell?.logo}
                      width="16px"
                      height="16px"
                      alt=""
                    />
                    <Typography variant="body1">
                      {values?.currencyToSell?.balance}{" "}
                      {values?.currencyToSell?.currency}
                    </Typography>
                  </>
                )}
              </Box>
              <div>
                <Typography variant="body2" fontWeight="600">
                  {t("currentRate")}
                </Typography>
                {preloading ? (
                  <Skeleton width={100} />
                ) : (
                  <Typography variant="body1">
                    {}1 {values?.currencyToSell?.currency} ≈{" "}
                    {values.currencyToSell?.id != 1
                      ? values?.currencyToSell?.rate
                      : values?.currencyToBuy?.rate}{" "}
                    {values?.currencyToBuy?.currency}
                  </Typography>
                )}
              </div>
              <Box display="flex" alignItems="start">
                <div>
                  <Typography variant="body2" fontWeight="600">
                    {t("rateFor24")}
                  </Typography>
                  {preloading ? (
                    <Skeleton width={100} />
                  ) : (
                    <Typography
                      variant="body1"
                      sx={{
                        color:
                          values?.currencyToBuy?.difference &&
                          values?.currencyToBuy?.difference[0] === "+"
                            ? "#10B981"
                            : values?.currencyToBuy?.difference &&
                              values?.currencyToBuy?.difference[0] === "-"
                            ? "#EF4444"
                            : "#A0ABBB",
                      }}
                    >
                      {values?.currencyToBuy?.difference === undefined
                        ? "0.00"
                        : values?.currencyToBuy?.difference}
                    </Typography>
                  )}
                </div>
                <ClickAwayListener onClickAway={() => setOpen(false)}>
                  <Tooltip
                    PopperProps={{
                      disablePortal: true,
                    }}
                    title={t("warning")}
                    placement="top-start"
                    onClose={() => setOpen(false)}
                    open={open}
                    disableFocusListener
                    disableHoverListener
                    disableTouchListener
                  >
                    <Button
                      sx={{
                        p: "8px!important",
                        minWidth: "unset!important",
                      }}
                      onClick={() => setOpen(true)}
                    >
                      <QuestionIcon />
                    </Button>
                  </Tooltip>
                </ClickAwayListener>
              </Box>
              <Box display={{ xs: "none", md: "block" }}>
                <Typography variant="body2" fontWeight="600">
                  {t("MinAmount")}
                </Typography>
                {preloading ? (
                  <Skeleton width={100} />
                ) : (
                  <Typography variant="body1">
                    {values?.currencyToBuy?.currency === "USDT"
                      ? 10
                      : values?.currencyToBuy?.min_qty}{" "}
                    {values?.currencyToBuy?.currency}
                  </Typography>
                )}
              </Box>
            </Box>
            <Button
              variant="contained"
              fullWidth
              type="submit"
              sx={{ mt: 2 }}
              disabled={!values.sumToBuy || !values.sumToSell || loading}
              className={classes.button}
              size="large"
              color="primary"
            >
              {loading ? (
                <CircularProgress
                  color="secondary"
                  sx={{
                    "&.MuiCircularProgress-root": {
                      width: "30px!important",
                      height: "30px!important",
                    },
                  }}
                />
              ) : (
                t("exchange")
              )}
            </Button>
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default Exchange;

const useStyles = makeStyles((theme) => ({
  button: {
    height: 56,
    width: "100%",
    display: "block",
    marginTop: 16,
  },

  formControlFirst: {
    boxShadow: "none",
    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  formControl: {
    backgroundColor: "#FFF",
    marginBottom: 16,

    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  img: {
    width: 30,
    height: 30,
    marginRight: 10,
    [theme.breakpoints.down(768)]: {
      display: "none",
    },
  },

  calculator: {
    marginTop: 20,
  },
  icon: {
    [theme.breakpoints.down(768)]: {
      transform: "rotate(90deg)",
    },
  },
}));
