import React, { useLayoutEffect, useRef, useState } from "react";
import { Dialog, Typography, Button, CircularProgress } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { setBuyConfirm } from "../../../../redux/reducers/buy";
import { setBuyData, confirmBuy } from "../../../../redux/reducers/buy1";
import { getСurrenciesList } from "../../../../redux/reducers/Сurrencies";

const ConfirmBuy = ({ t, setLoadingMain }) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const firstUpdate = useRef(true);

  const [loading, setLoading] = useState(false);

  const { buy, buy_confirmation } = useSelector(({ buy }) => buy);

  const confirm = () => {
    // confirm transaction
    dispatch(confirmBuy(buy?.message?.confirm?.id));
    // show loader
    setLoading(true);
  };

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (buy_confirmation.severity === 200) {
      // end all
      dispatch(setBuyConfirm({ ...buy_confirmation, open: false }));
      dispatch(setBuyData({ ...buy, open: false }));

      // update balances
      dispatch(getСurrenciesList());
      setTimeout(() => {
        // navigate to history
        history.push("/dashboard/history");
      }, 500);
    }

    if (buy_confirmation.severity) {
      // hide loader
      setLoading(false);
    }

    // eslint-disable-next-line
  }, [buy_confirmation?.severity]);

  return (
    <Dialog
      open={buy.open}
      sx={{
        "& .MuiPaper-root": {
          borderRadius: 2,
          width: "100%",
          padding: "40px 31px!important",
        },

        "& ul": {
          "& li": {
            fontWeight: 300,
            mb: 2,
            display: "flex",
            justifyContent: "space-between",
            whiteSpace: "nowrap",
            alignItems: "baseline",

            "& div": {
              width: "100%",
              height: "fit-content",
              borderBottom: "2px dotted #B8C0CC",
              margin: "0 3px",
            },

            "& span": {
              fontWeight: 500,
            },
          },
        },
      }}
      onClose={() => {
        if (!loading) {
          setLoadingMain(false);
          dispatch(
            setBuyData({
              ...buy,
              open: false,
            })
          );
        }
      }}
    >
      <Typography variant="h2" fontWeight="500" textAlign="center" mb={3}>
        {t("paymentСonfirmation")}
      </Typography>
      <ul>
        <Typography variant="h5" component="li">
          {t("amountToBeCredited")}
          <div></div>
          <span>
            {buy?.message?.confirm?.debit} {buy?.message?.confirm?.currency}
          </span>
        </Typography>
        <Typography variant="h5" component="li">
          {t("amountToPay")}
          <div></div>
          <span>{buy?.message?.confirm?.amount} USDT</span>
        </Typography>
        <Typography variant="h5" component="li">
          {t("Commission")}
          <div></div>
          <span>{buy?.message?.confirm?.commission} USDT</span>
        </Typography>
      </ul>
      <Button
        variant="contained"
        color="primary"
        size="large"
        onClick={confirm}
        disabled={loading}
      >
        {loading ? (
          <CircularProgress className="progress" color="primary" />
        ) : (
          t("confirm")
        )}
      </Button>
    </Dialog>
  );
};

export default ConfirmBuy;
