import { Button, CircularProgress, Skeleton } from "@mui/material";
import {
  InputAdornment,
  Slider,
  Tab,
  Tabs,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Orders from "../../../components/Orders";
import Inputs from "../../../components/Orders/Inputs";
import {
  confirmBuy,
  setBuyConfirmationData,
  setBuyData,
} from "../../../redux/reducers/buy1";
import { getBalances } from "../../../redux/reducers/myProfile";
import {
  confirmSell,
  setSellConfirmationData,
  setSellData,
} from "../../../redux/reducers/sell1";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const Fields = ({
  loading,
  setLoading,
  СurrenciesLoading,
  dealType,
  currency,
  t,
  formik,
  balance,
  setOrder,
  order,
  values,
  setResponse,
  setDealType,
}) => {
  const dispatch = useDispatch();

  function valuetext(value) {
    return `${value}%`;
  }

  const { buy, buy_confirmation } = useSelector(({ buy }) => buy);
  const { sell, sell_confirmation } = useSelector(({ sell }) => sell);

  useEffect(() => {
    if (dealType === "buy") {
      if (buy.severity === 200) {
        dispatch(confirmBuy(buy?.message?.confirm?.id));
        dispatch(setBuyData({ ...buy, severity: null }));
      } else if (buy.severity) {
        setLoading(false);
        setResponse({
          ...buy,
          open: true,
        });
      }
    } else if (dealType === "sell") {
      if (sell.severity === 200) {
        dispatch(confirmSell(sell?.message?.confirm?.id));
        dispatch(setSellData({ ...sell, severity: null }));
      } else if (sell.severity) {
        setLoading(false);
        setResponse({
          ...sell,
          open: true,
        });
      }
    }

    // eslint-disable-next-line
  }, [buy, sell]);

  useEffect(() => {
    if (sell_confirmation.severity === 200 && sell_confirmation.open) {
      setLoading(false);
      formik.resetForm();
      dispatch(getBalances());
      dispatch(getСurrenciesList());
      setTimeout(
        () => dispatch(setSellConfirmationData({ ...sell, open: false })),
        3200
      );
    }
    setResponse(
      sell_confirmation.severity === 200
        ? {
            ...sell_confirmation,
            message: { messages: t("dealSuccessfullyCreated") },
          }
        : sell_confirmation
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sell_confirmation]);

  useEffect(() => {
    if (buy_confirmation.severity === 200 && buy_confirmation.open) {
      setLoading(false);
      formik.resetForm();
      dispatch(getBalances());
      dispatch(getСurrenciesList());
      setTimeout(
        () => dispatch(setBuyConfirmationData({ ...buy, open: false })),
        3200
      );
    }
    setResponse(
      buy_confirmation.severity === 200
        ? {
            ...buy_confirmation,
            message: { messages: t("dealSuccessfullyCreated") },
          }
        : buy_confirmation
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [buy_confirmation]);

  useEffect(() => {
    formik.setFieldValue("amountInUSDT", "");

    // eslint-disable-next-line
  }, [dealType]);

  useEffect(() => {
    if (dealType === "buy" && values.amountInUSDT) {
      formik.setFieldValue(
        "total",
        (values.amountInUSDT / values.price)?.toFixed(currency?.decimal)
      );
    } else
      formik.setFieldValue(
        "total",
        (values.amountInUSDT * values.price)?.toFixed(currency?.decimal)
      );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formik.values.amountInUSDT, formik.values.price]);

  const handleChange = (type, value) => {
    setDealType(value);
  };

  const marks = [
    {
      value: 0,
      label: "0%",
    },
    {
      value: 25,
      label: "25%",
    },
    {
      value: 50,
      label: "50%",
    },
    {
      value: 75,
      label: "75%",
    },
    {
      value: 100,
      label: "100%",
    },
  ];

  return (
    <Box
      component="form"
      height="100%"
      display="flex"
      flexDirection="column"
      justifyContent="space-between"
      onSubmit={formik.handleSubmit}
    >
      <div>
        <Tabs
          variant="scrollable"
          sx={{
            mb: 2,
            background: "#FFFFFF",
            boxShadow: " 0px 1px 0px #E2E8F0",
            borderRadius: 1,
            ".MuiTabs-flexContainer": {
              width: "100%",
              flexDirection: "row!important",
            },

            "& .MuiTabs-indicator": {
              display: "block",
              backgroundColor: dealType === "buy" ? "#0b9981" : "#FF3B69",
            },

            "& button": {
              width: "50%",
              color: "#FFF",

              "&.Mui-selected": {
                fontWeight: 700,
              },

              "&:first-child": {
                color: "#323A46!important",
                "&.Mui-selected": {
                  color: "#0b9981!important",
                },
              },

              "&:last-child": {
                color: "#323A46!important",
                "&.Mui-selected": {
                  color: "#f23646!important",
                },
              },
            },
          }}
          value={dealType}
          onChange={handleChange}
        >
          <Tab value="buy" label={t("buy")} />
          <Tab value="sell" label={t("sell")} />
        </Tabs>
        <Box mb={2}>
          <Orders t={t} trade setOrder={setOrder} order={order} />
        </Box>

        <Inputs
          order={order}
          parameter={currency?.currency}
          t={t}
          formik={formik}
          handleValueChange={formik.handleChange}
        />
        <TextField
          type="text"
          fullWidth
          onKeyPress={(event) => {
            if (!/[0-9.]/.test(event.key)) {
              event.preventDefault();
            }
          }}
          variant="outlined"
          name="amountInUSDT"
          value={formik.values.amountInUSDT}
          onChange={formik.handleChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">{t("amount")}:</InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                {dealType === "sell" ? currency.currency : "USDT"}
              </InputAdornment>
            ),
            inputProps: {
              pattern: "\\d*.\\d*",
            },
          }}
        />
        <TextField
          variant="outlined"
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">{t("total")}:</InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                {dealType === "sell" ? "USDT" : currency?.currency}
              </InputAdornment>
            ),
            inputProps: {
              pattern: "\\d*.\\d*",
            },
          }}
          fullWidth
          name="total"
          value={formik.values?.total}
          onChange={formik.handleChange}
        />
        <Box p="8px 16px">
          <Slider
            defaultValue={0}
            disabled={
              (dealType === "buy" && balance <= 0) ||
              (dealType === "sell" && currency?.balance <= 0)
            }
            getAriaValueText={valuetext}
            valueLabelDisplay="auto"
            step={null}
            marks={marks}
            sx={{
              "& .MuiSlider-thumb": {
                color: "#FCE34D!important",
                border: "4px solid #FFF",
                height: 24,
                width: 24,
                boxShadow: "none",
              },

              "&.MuiSlider-root": {
                color: "#323A46!important",
              },

              "& .MuiSlider-mark": {
                backgroundColor: "#bfbfbf",
                height: 8,
                width: "5px!important",
                borderRadius: 1,
                "&.MuiSlider-markActive": {
                  opacity: 1,
                  backgroundColor: "currentColor",
                },
              },
            }}
            onChange={(e) => {
              if (dealType === "sell") {
                formik.setFieldValue(
                  "amountInUSDT",
                  (currency?.balance * e.target.value) / 100
                );
                formik.setFieldValue(
                  "total",
                  ((currency?.balance * currency.rate) / 100) * e.target.value
                );
              } else {
                formik.setFieldValue(
                  "amountInUSDT",
                  (balance * e.target.value) / 100
                );
              }
            }}
            min={0}
            max={100}
          />
        </Box>
        <Typography color="#323A46" fontWeight="400" variant="h5" mb={0.5}>
          {t("availableAmount")}{" "}
          {!СurrenciesLoading ? (
            <Skeleton width="30%" sx={{ display: "inline-block" }} />
          ) : (
            <span style={{ fontWeight: "700" }}>
              {dealType === "buy" ? balance : currency?.balance}{" "}
              {dealType === "buy" ? "USDT" : currency?.currency}
            </span>
          )}
        </Typography>
        <Typography color="#323A46" fontWeight="400" variant="h5" mb={1}>
          {t("MinAmount")}:{" "}
          {!СurrenciesLoading ? (
            <Skeleton width="30%" sx={{ display: "inline-block" }} />
          ) : (
            <span style={{ fontWeight: "700" }}>
              {currency?.min_qty} {currency?.currency}
            </span>
          )}
        </Typography>
      </div>
      <Button
        size="large"
        fullWidth
        color="primary"
        type="submit"
        disabled={!values.amountInUSDT || loading}
        style={{ color: "#000" }}
        variant="contained"
      >
        {loading ? (
          <CircularProgress
            sx={{
              "&.MuiCircularProgress-root": {
                width: "20px!important",
                height: "20px!important",
              },
            }}
            color="secondary"
          />
        ) : (
          t("confirm")
        )}
      </Button>
    </Box>
  );
};

export default Fields;
