import {
  IconButton,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { setCurrenciesDrawer } from "../../../redux/reducers/Main";
import close from "../../../assets/images/state/close.svg";

const Currencies = ({ t, setCurrency, currency, formik }) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [search, setSearch] = useState("");

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const { isCurrenciesDraderOpen } = useSelector(
    ({ MainReducer }) => MainReducer
  );

  const searchItem = useMemo(
    () =>
      СurrenciesList.filter((item) =>
        item.currency
          .toLocaleLowerCase()
          .startsWith(search.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [search]
  );

  const Сurrencies = search ? searchItem : СurrenciesList;

  return (
    <>
      <Box
        display="flex"
        backgroundColor="#F7F8F9"
        alignItems="center"
        columnGap="16px"
        justifyContent="space-between"
      >
        <TextField
          fullWidth
          value={search}
          sx={{
            "&.MuiFormControl-root": {
              background: "#FFFFFF",
              boxShadow: "0px 1px 0px #E2E8F0",
              borderRadius: "8px",
            },

            "& fieldset": {
              borderColor: "transparent",
            },
          }}
          onChange={(e) => {
            setSearch(e.target.value);
          }}
          variant="outlined"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <svg
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M11.5 21C16.7467 21 21 16.7467 21 11.5C21 6.25329 16.7467 2 11.5 2C6.25329 2 2 6.25329 2 11.5C2 16.7467 6.25329 21 11.5 21Z"
                    stroke="#B8C0CC"
                    strokeWidth="1.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M22 22L20 20"
                    stroke="#B8C0CC"
                    strokeWidth="1.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              </InputAdornment>
            ),
          }}
          placeholder={t("tickerOrPair")}
        />
        <IconButton
          onClick={() =>
            dispatch(
              setCurrenciesDrawer({ ...isCurrenciesDraderOpen, open: false })
            )
          }
        >
          <img src={close} alt="" />
        </IconButton>
      </Box>
      <Box sx={{ height: "100%", overflow: "scroll" }}>
        <Box
          mt={2}
          component="table"
          sx={{
            display: "table",
            width: "100%",
            borderSpacing: 0,

            "& thead  td": {
              pb: 1,
            },

            // "& thead  p": {
            //   textAlign: "center",
            // },

            "& tbody tr": {
              transition: "all 0.1s linear",
              "&:hover": {
                backgroundColor: "#ffffff50",
              },
            },

            "& tbody td ": {
              cursor: "pointer",

              "& p": {
                p: 1,
              },

              "& img": {
                pl: 1,
              },
            },

            "& tbody td:last-child ": {
              textAlign: "end",
              pr: 1,
            },
          }}
        >
          <thead>
            <td>
              <Typography variant="body2" color="#64748B" fontWeight="400">
                Пары:
              </Typography>
            </td>
            <td>
              <Typography variant="body2" color="#64748B" fontWeight="400">
                Цена:
              </Typography>
            </td>
            <td>
              <Typography variant="body2" color="#64748B" fontWeight="400">
                Изменения:
              </Typography>
            </td>
          </thead>
          <tbody>
            {Array.isArray(Сurrencies) &&
              Сurrencies
                .filter((item) => item.currency !== "USDT")
                .map((item) => (
                  <tr
                    style={{
                      backgroundColor:
                        currency?.currency === item.currency && "#ffffff50",
                    }}
                    onClick={() => {
                      dispatch(
                        setCurrenciesDrawer({
                          currency: item.currency,
                          name: item.name,
                          logo: item.logo,
                          open: false,
                        })
                      );
                      history.push(`/dashboard/trade/${item.currency}`);
                      setCurrency(item);
                      formik.setFieldValue("price", item.rate);
                    }}
                  >
                    <td
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <img
                        style={{ objectFit: "contain", marginRight: 8 }}
                        width="30"
                        height="30"
                        src={item.logo}
                        alt=""
                      />
                      <Typography variant="body2" color="#000">
                        <span style={{ fontWeight: 600 }}>{item.currency}</span>{" "}
                        <span style={{ fontWeight: 400, color: "#64748B" }}>
                          {item.name}
                        </span>
                      </Typography>
                    </td>
                    <td>
                      <Typography variant="body2" fontWeight="600" color="#000">
                        {item.rate}
                      </Typography>
                    </td>
                    <td>
                      <Typography
                        variant="body2"
                        sx={{
                          fontWeight: 600,
                          textAlign: "center",
                          background:
                            item?.difference && item.difference[0] === "+"
                              ? "#ECFDF5"
                              : item?.difference && item.difference[0] === "-"
                              ? "#FEF2F2"
                              : "#F7F8F9",
                          padding: "4px 0!important",
                          borderRadius: "8px",
                        }}
                        color={
                          item?.difference && item.difference[0] === "+"
                            ? "#10B981!important"
                            : item?.difference && item.difference[0] === "-"
                            ? "#EF4444!important"
                            : "#A0ABBB!important"
                        }
                      >
                        {" "}
                        {item?.difference === undefined
                          ? "0.00"
                          : item?.difference}
                      </Typography>
                    </td>
                  </tr>
                ))}
          </tbody>
        </Box>
      </Box>
    </>
  );
};

export default Currencies;
