import { Container, Grid } from "@mui/material";
import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import CryptoBlock from "../../../components/CryptoBlock";
import CryptoSkeleton from "../../../components/CryptoSkeleton";
import { getBalances } from "../../../redux/reducers/myProfile";

const Balances = ({ t }) => {
  const dispatch = useDispatch();
  const [filter, setFilter] = useState("");

  const { СurrenciesLoading, СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  useEffect(() => {
    dispatch(getBalances());
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const filteredCoin = useMemo(
    () =>
      СurrenciesList?.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const Сurrencies = filter ? filteredCoin : СurrenciesList;

  return (
    <section>
      <Container maxWidth="lg">
        <Balance />
        <CabinetTop
          input
          t={t}
          title={t("YourWallets")}
          setFilter={setFilter}
        />
        <Grid container spacing={2}>
          {!СurrenciesLoading
            ? Array.from(Array(12).keys()).map((item, idx) => (
                <Grid item xs={12} sm={6} md={3} lg={3} xl={3}>
                  <CryptoSkeleton key={idx} style={{ width: 389 }} />
                </Grid>
              ))
            : Сurrencies?.map((item) => (
                <Grid item xs={12} sm={6} md={3} lg={3} xl={3}>
                  <CryptoBlock
                    item={item}
                    href={
                      item.currency === "USDT"
                        ? `/dashboard/withdraw/${item.currency}`
                        : `/dashboard/buy/details/${item.currency}`
                    }
                  />
                </Grid>
              ))}
        </Grid>
      </Container>
    </section>
  );
};

export default Balances;
