export default Form;

const useStyles = makeStyles(() => ({
  formControl: {
    "& .MuiSelect-select": {
      backgroundColor: "#F7F8F9!important",
    },
    marginBottom: "0px!important",

    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiOutlinedInput-root": {
      backgroundColor: "#F7F8F9!important",
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },
}));
