import React, { lazy, useEffect, useState } from "react";
import { Redirect, Route, Switch, useLocation } from "react-router";
import HeaderCabinet from "../../shared/HeaderCabinet";
import TemporaryDrawer from "../../components/Drawer";
import Balances from "../../pages/dashboard/Balances";
import NotFound from "../../pages/NotFound";
import LeaveFeedback from "../../pages/Reviews/LeaveFeedback";
import OrderHistory from "../../pages/dashboard/OrderHistory";
import Exchange from "../../pages/dashboard/Exchange";
import Trade from "../../pages/dashboard/Trade";
import { makeStyles } from "@mui/styles";
import { Alert, Snackbar } from "@mui/material";

const AllTransactions = lazy(() =>
  import("../../pages/dashboard/MoneyTransaction/AllTransactions")
);
const PaymentForServices = lazy(() =>
  import("../../pages/dashboard/PaymentForServices")
);
const GetMoney = lazy(() =>
  import("../../pages/dashboard/MoneyTransaction/GetMoney")
);
const SendMoney = lazy(() =>
  import("../../pages/dashboard/MoneyTransaction/SendMoney")
);
const WithdrawalDetails = lazy(() =>
  import("../../pages/dashboard/Withdrawal/WithdrawalDetails")
);
const AllCourses = lazy(() => import("../../pages/dashboard/AllCourses"));
const History = lazy(() => import("../../pages/dashboard/History"));
const BuyDetails = lazy(() =>
  import("../../pages/dashboard/Buy/buyDetails/index")
);
const Replenish = lazy(() => import("../../pages/dashboard/Replenish"));
const Settings = lazy(() => import("../../pages/dashboard/Settings"));
const Buy = lazy(() => import("../../pages/dashboard/Buy/index"));
const Withdrawal = lazy(() => import("./../../pages/dashboard/Withdrawal"));
const Sell = lazy(() => import("./../../pages/dashboard/sell"));
const SellDetails = lazy(() =>
  import("./../../pages/dashboard/sell/sellDetail")
);
const Main = lazy(() => import("../../pages/dashboard/Main"));
const P2P = lazy(() => import("../../pages/dashboard/P2P"));
const MoneyTransaction = lazy(() =>
  import("../../pages/dashboard/MoneyTransaction")
);
const PaymentFSDetails = lazy(() =>
  import("../../pages/dashboard/PaymentForServices/Details")
);
const Services = lazy(() =>
  import("../../pages/dashboard/PaymentForServices/Services")
);

const CabinetRoute = ({ t, i18n, mode }) => {
  const classes = useStyles();
  const location = useLocation();

  const [loading, setLoading] = useState(false);
  const [verificationPush, setVerificationPush] = useState(false);

  useEffect(() => {
    document.title = t("dashboard");
  }, []);
  return (
    <>
      <Snackbar
        open={verificationPush}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => setVerificationPush(false)}
      >
        <Alert severity="error" variant="filled">
          {t("youNeedVerification")}
        </Alert>
      </Snackbar>
      <HeaderCabinet t={t} i18n={i18n} />
      {/* <CoursesLine /> */}
      <main
        className={
          location.pathname.includes("dashboard/buy/details/") ||
          location.pathname.includes("dashboard/sell/details/") ||
          location.pathname.includes("dashboard/p2p") ||
          location.pathname.includes("dashboard/trade")
            ? null
            : classes.main
        }
      >
        <Switch>
          <Route
            exact
            path="/dashboard/main"
            component={() => (
              <Main
                mode={mode}
                setVerificationPush={setVerificationPush}
                t={t}
                i18n={i18n}
              />
            )}
          />
          <Route
            path="/dashboard/Settings"
            component={() => <Settings t={t} i18n={i18n} />}
          />
          <Route
            path="/dashboard/replenish"
            component={() => (
              <Replenish
                mode={mode}
                setVerificationPush={setVerificationPush}
                t={t}
                i18n={i18n}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/withdraw"
            component={() => (
              <Withdrawal
                mode={mode}
                setVerificationPush={setVerificationPush}
                t={t}
                i18n={i18n}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/buy"
            setVerificationPush={setVerificationPush}
            component={() => (
              <Buy
                t={t}
                mode={mode}
                setVerificationPush={setVerificationPush}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/buy/details/:parameter"
            component={() => <BuyDetails t={t} />}
          />
          <Route
            exact
            path="/dashboard/sell"
            component={() => (
              <Sell
                mode={mode}
                setVerificationPush={setVerificationPush}
                t={t}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/sell/details/:parameter"
            component={() => <SellDetails t={t} />}
          />
          <Route
            exact
            path="/dashboard/withdraw/:coin"
            component={() => <WithdrawalDetails t={t} />}
          />
          <Route exact path="/dashboard/p2p" component={() => <P2P t={t} />} />
          {/* <Route
            exact
            path="/dashboard/p2p/deals"
            component={() => <Deals t={t} />}
          /> */}
          <Route
            exact
            path="/dashboard/rates"
            component={() => <AllCourses t={t} i18n={i18n} />}
          />
          <Route
            exact
            path="/dashboard/history"
            component={() => <History t={t} i18n={i18n} />}
          />
          <Route
            exact
            path="/dashboard/transaction"
            component={() => <MoneyTransaction t={t} i18n={i18n} />}
          />
          {/* <Route
            exact
            path="/dashboard/p2p/createDeals"
            component={() => <CreateDeals t={t} />}
          /> */}
          <Route
            exact
            path="/dashboard/transaction/send"
            component={() => <SendMoney t={t} />}
          />
          <Route
            exact
            path="/dashboard/transaction/get/:id"
            component={() => <GetMoney t={t} />}
          />
          <Route
            exact
            path="/dashboard/transaction/history"
            component={() => <AllTransactions t={t} />}
          />
          {/* <Route
            exact
            path="/dashboard/p2p/PreviewDeal"
            component={() => <PreviewDeal t={t} />}
          /> */}
          {/* <Route
            exact
            path="/dashboard/p2p/joinDeals/:id"
            component={() => <JoinDeals t={t} />}
          /> */}
          <Route
            exact
            path="/dashboard/payment-for-services"
            component={() => <PaymentForServices mode={mode} t={t} />}
          />
          <Route
            exact
            path="/dashboard/payment-for-services/:servicesId"
            component={() => (
              <Services
                mode={mode}
                setVerificationPush={setVerificationPush}
                t={t}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/payment-for-services/:servicesId/:detailsId"
            component={() => (
              <PaymentFSDetails
                setLoading={setLoading}
                loading={loading}
                t={t}
              />
            )}
          />
          <Route
            exact
            path="/dashboard/balances"
            component={() => <Balances t={t} />}
          />
          <Route
            exact
            path="/dashboard/exchange/"
            component={() => <Exchange t={t} />}
          />
          <Redirect exact from="/dashboard/trade/" to="/dashboard/trade/BTC" />
          <Route
            exact
            path="/dashboard/trade/:currencyId"
            component={() => <Trade t={t} />}
          />
          <Route
            exact
            path="/dashboard/reviews/leave"
            component={() => <LeaveFeedback t={t} />}
          />
          <Route
            exact
            path="/dashboard/orders"
            component={() => <OrderHistory t={t} />}
          />
          <Route path="*" exact={true} component={NotFound} />
        </Switch>
        <TemporaryDrawer t={t} i18n={i18n} />
      </main>
    </>
  );
};

export default CabinetRoute;

const useStyles = makeStyles((theme) => ({
  main: {
    background: "#F7F8F9",

    "& section": {
      padding: "40px 0 80px",
      [theme.breakpoints.down(768)]: {
        padding: "40px 0 120px",
      },
    },

    "& .img": {
      width: "100%",
      objectFit: "cover",
      borderRadius: 8,
      height: "100%",
    },
  },
}));
