import React, { useEffect, useState } from "react";
import { Pagination, Stack } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import { useFormik } from "formik";
import {
  getOrderList,
  getOrderListExecuted,
} from "../../../../redux/reducers/orders";
import { getHistory } from "../../../../redux/reducers/Operations";
import Form from "./Form";
import ActiveOrders from "./ActiveOrders";
import HistoryOrders from "./HistoryOrders";
import Deals from "./Deals";

const Orders = ({ t }) => {
  const dispatch = useDispatch();

  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [order, setOrder] = useState("active_orders");

  const { order_list, order_list_executed, order_data } = useSelector(
    ({ orders }) => orders
  );
  const { history, total } = useSelector(
    ({ OperationsReducer }) => OperationsReducer
  );

  const formik = useFormik({
    initialValues: {
      operation_type: "",
      date_from: "",
      date_to: "",
    },
    onSubmit: (values) => {
      if (order === "active_orders") {
        dispatch(
          getOrderList({
            order_type: values.operation_type === 5 ? 2 : 1,
            date_from: values.date_from,
            date_to: values.date_to,
          })
        );
      } else if (order === "history_orders")
        dispatch(
          getOrderListExecuted({
            order_type: values.operation_type === 5 ? 2 : 1,
            date_from: values.date_from,
            date_to: values.date_to,
          })
        );
      else dispatch(getHistory(values));
    },
  });

  useEffect(() => {
    setCount(Math.ceil(total / 20));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [history, order]);

  useEffect(() => {
    setPage(1);
  }, [formik.values]);

  const changeHandler = (event, value) => {
    setPage(value);
    if (order === "history_orders") {
      dispatch(
        getOrderList({
          page: value,
          order_type: formik.values.operation_type === 5 ? 2 : 1,
          date_from: formik.values.date_from,
          date_to: formik.values.date_to,
        })
      );
    } else if (order === "active_orders")
      dispatch(
        getOrderListExecuted({
          page: value,
          order_type: formik.values.operation_type === 5 ? 2 : 1,
          date_from: formik.values.date_from,
          date_to: formik.values.date_to,
        })
      );
    else
      dispatch(
        getHistory({
          page: value,
          operation_type: formik.values.operation_type,
          date_from: formik.values.date_from,
          date_to: formik.values.date_to,
        })
      );
  };

  return (
    <>
      <Form setOrder={setOrder} t={t} formik={formik} order={order} />
      <Box
        height="30%"
        sx={{
          overflow: "scroll",
        }}
        pb={25}
        mt={2}
      >
        {order === "active_orders" && (
          <ActiveOrders order_data={order_data} order_list={order_list} t={t} />
        )}
        {order === "history_orders" && (
          <HistoryOrders order_list_executed={order_list_executed} t={t} />
        )}
        {order === "deals" && <Deals history={history} t={t} />}
        {count > 0 && order === "deals" && (
          <Stack
            spacing={2}
            mt={"50px"}
            sx={{
              "& nav": {
                display: "flex",
                justifyContent: "center",

                "& button": {
                  minWidth: { xs: "20px!important", md: "40px!important" },
                },
              },
            }}
          >
            <Pagination
              count={count}
              size="large"
              page={page}
              onChange={changeHandler}
            />
          </Stack>
        )}
      </Box>
    </>
  );
};

export default Orders;
